 = ref(1);\nconst paginatedData = ref<Array<ISDActivities>>([]);\nconst selectedItemsPerPage = ref('10');\nconst itemsPerPageOptions = ref(['10', '20', '30', 'All']);\nconst searchUser = ref(\"\");\n\nbackofficeUseCase.setBreadcrumb([\"Users\"]);\n\nexecutor.execute(async () => {\n    sdActivities.value = await backofficeUseCase.listServiceDesignerActivities();\n}, { loading: true });\n\n\nconst totalPages = computed(() => {\n    if(selectedItemsPerPage.value != 'All') {\n        return Math.ceil(getUserList().length / parseInt(selectedItemsPerPage.value))\n    }\n    else {\n        return 1;\n    }\n});\n\nconst displayedPageNumbers = computed(() => {\n    const range = 2; // Number of page numbers to display on each side of the current page\n    let startPage = Math.max(1, currentPage.value - range);\n    let endPage = Math.min(totalPages.value, currentPage.value + range);\n    if (endPage - startPage < 2 * range) {\n    // Adjust page numbers to always show the same number of items\n    if (startPage === 1) {\n        endPage = Math.min(endPage + (2 * range - (endPage - startPage)), totalPages.value);\n    } else if (endPage === totalPages.value) {\n        startPage = Math.max(startPage - (2 * range - (endPage - startPage)), 1);\n    }\n    }\n    return Array.from({ length: endPage - startPage + 1 }, (_, index) => startPage + index);\n});\n\nwatchEffect(() => {\n    if(selectedItemsPerPage.value != 'All') {\n        const startIndex = (currentPage.value - 1) * parseInt(selectedItemsPerPage.value);\n        const endIndex = startIndex + parseInt(selectedItemsPerPage.value);\n        const newData = getUserList().slice(startIndex, endIndex);\n        paginatedData.value = newData;\n    }\n    else {\n        paginatedData.value = getUserList();\n    }\n});\nfunction setCurrentPage(page: number) {\n    currentPage.value = page;\n}\nfunction onItemsPerPageSelect (itemCount: string) {\n    selectedItemsPerPage.value = itemCount;\n    setCurrentPage(1)\n}\nwatchEffect(() => {\n    currentPage.value = 1;\n});\nfunction formatDate(date: Date) {\n    const formatter = new Intl.DateTimeFormat('en-GB', { year: 'numeric', month: 'long', day: 'numeric', hour: 'numeric', minute: 'numeric' });\n    const formattedDate = formatter.format(new Date(date));\n    return formattedDate;\n}\nfunction getUserList() {\n    return sdActivities.value.filter((activity: ISDActivities) => (activity.username?.toUpperCase().includes(searchUser.value.toUpperCase())))\n}\n\n</script>\n\n<template>\n    <div class=\"page-title\">\n        <span>User List ({{ sdActivities?.length }})</span>\n    </div>\n    \n\n    <div class=\"filter-area\">\n        <div class=\"search-in-table\">\n            <i class=\"mdi mdi-magnify\"></i>\n            <input class=\"search-input\" type=\"text\" placeholder=\"Search\" alt=\"search\" v-model=\"searchUser\" @input=\"setCurrentPage(1)\"/>\n        </div>\n        <button class=\"btn_exportexcel\" @click='backofficeUseCase.downloadActivityXlsxFile(sdActivities)'>\n            <span>Export Excel</span>\n        </button>\n    </div>\n    <div style=\"padding-top:20px; padding-bottom: 50px;\">\n        <table class=\"users-table\">\n            <tr class=\"thead\">\n                <th >Name</th>\n                <th >Last Access </th>\n                <th >Total Access Count</th>\n            </tr>\n            <tr v-for=\"activity in paginatedData\">\n                <td>{{ activity.username}}</td>\n                <td>{{ formatDate(activity.lastAccessDate) }}</td>\n                <td>{{ activity.logs.length }}</td>\n            </tr>\n            <tr>\n                <td colspan=\"1\" style=\"display: flex; align-items: center;\">\n                    <span style=\"font-size: 14px; color: #616161\">Items per page:</span>\n                    <CustomSelect :options=\"itemsPerPageOptions\" @input=\"onItemsPerPageSelect\" class=\"items-per-page\" :selected-class=\"'item-count'\" :default=\"selectedItemsPerPage\"></CustomSelect>\n                </td>\n                <td colspan=\"9\">\n                    <div style=\"float: right; display: flex; align-items: center;\">\n                        <button class=\"change-page-number\" @click=\"currentPage--\" :disabled=\"currentPage === 1\">\n                            <i class=\"mdi mdi-chevron-left\" :style=\"{color: currentPage === 1 ? '#BCBCBC' : ''}\"></i>\n                        </button>\n                        <button v-for=\"pageNumber in displayedPageNumbers\" :key=\"pageNumber\" @click=\"setCurrentPage(pageNumber)\" :class=\"currentPage === pageNumber ? 'page-number active' : 'page-number'\"> {{ pageNumber }} </button>\n                        <button class=\"change-page-number\" @click=\"currentPage++\" :disabled=\"currentPage === totalPages\">\n                            <i class=\"mdi mdi-chevron-right\" :style=\"{color: currentPage === totalPages ? '#BCBCBC' : ''}\"></i>\n                        </button>\n                    </div>\n                </td>\n            </tr>\n        </table>\n    </div>\n</template>\n\n<style scoped>\n* {\n    font-family: \"Roboto\";\n}\n.page-title {\n    font-weight: 500;\n    font-size: 24px;\n    line-height: 28px;\n    color: #151233;\n}\n.filter-area {\n    margin: 20px 0;\n    display: flex;\n    align-items: center;\n}\n.search-in-table {\n    background: transparent;\n    border: 1px solid #BCBCBC !important;\n    border-radius: 4px !important;\n    height: 32px;\n    display: flex;\n    align-items: center;\n}\n.search-input {\n    background: transparent;\n    border: none;\n}\n.search-input:focus {\n    outline: none;\n}\n.search-in-table i {\n    color: #BCBCBC;\n    font-size: 20px;\n    padding: 0px 4px;\n}\n\n.view-btn {\n    background: none;\n    border:none;\n    color: #2C5A86;\n    font-weight: 500;\n    font-size: 14px;\n    line-height: 16px;\n}\n.users-table {\n    background: #FFFFFF;\n    border: 1px solid #E3E3E3;\n    box-shadow: 1px 1px 2px rgba(33, 33, 33, 0.16);\n    border-radius: 10px;\n    width: 100%;\n}\ntd:hover {\n    background-color: none !important;\n}\ntable tr:hover:not(:last-child):not(:first-child) {\n    background-color: #EEEEEE;\n}\n.users-table th\n    {\n    border-bottom: 1px solid #E3E3E3;\n    padding: 18px 36px\n}\n.users-table td {\n    border-bottom: 1px solid #E3E3E3;\n    padding: 20px 36px;\n    color: #212121;\n    font-size: 14px;\n    line-height: 16px;\n}\n.users-table .thead {\n    background: #F5F5F5;\n    color: #616161;\n    font-weight: 500;\n    font-size: 14px;\n    line-height: 16px;\n}\n.items-per-page {\n    background: #FFFFFF;\n    border: 1px solid #BCBCBC;\n    border-radius: 4px;\n    width: 84px;\n    padding: 7px 12px;\n    margin-left: 12px\n}\n.filters {\n    width: 115px;\n    background: transparent;\n    border: 1px solid #BCBCBC;\n    border-radius: 4px;\n    padding: 7px 12px;\n    margin-left: 20px;\n}\n.page-number {\n    padding: 8px 12px;\n    color: #616161;\n    font-size: 14px;\n    line-height: 16px;\n    background: none;\n    border: none\n}\n.page-number.active {\n    background: #151233;\n    border-radius: 4px;\n    color: #FFFFFF;\n}\n.change-page-number {\n    border: none;\n    background: none;\n}\n.change-page-number i {\n    color: #151233; \n    font-size: 20px; \n    line-height: 20px;\n}\n.quick-filters {\n    display: flex;\n    align-items: center;\n    margin-left: 20px;\n}\n.quick-filters label {\n    font-weight: 500;\n    font-size: 14px;\n    line-height: 16px;\n    color: #616161;\n    margin-right: 4px;\n}\n\n.quick-filters span {\n    color: #2C5A86;\n    font-weight: 500;\n    font-size: 14px;\n    line-height: 16px;\n    padding: 8px 16px;\n    margin-right: 4px;\n    cursor: pointer;\n}\n.quick-filters span:hover {\n    background: #D5D5D5;\n    border-radius: 4px;\n}\n.quick-filters span.active {\n    background: #2C5A86;\n    border-radius: 4px;\n    color: #FFFFFF;\n}\n.btn_exportexcel {\n    background: #ff6b6b;\n    border-radius: 4px;\n    border: none;\n    padding: 8px 16px;\n    font-size: 14px;\n    font-weight: 500;\n    color: #FFFFFF;\n    display: flex;\n    flex-direction: row;\n    align-items: center;\n    height: 32px;\n    margin-left: 20px;\n    margin-left: auto;\n}\n</style>\n<style>\n    .item-count,\n    .selected-filter {\n        color: #212121;\n        font-size: 14px;\n        line-height: 16px;\n    }\n    .items-per-page .items,\n    .filters .items {\n        color: #212121;\n        border-radius: 8px;\n        border: 1px solid #E3E3E3;\n        overflow: hidden;\n        background-color: #FFFFFF;\n        box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n        margin-top: 8px;\n    }\n    .items-per-page .items .option,\n    .filters .items .option {\n        color: #212121;\n        padding-left: 12px;\n        line-height: 32px;\n    }\n    .items-per-page .items .option:hover,\n    .filters .items .option:hover {\n        background-color: #EEEEEE;\n    }\n    .items-per-page i,\n    .filters i {\n        font-size: 16px;\n        color: #BCBCBC;\n    }\n</style>"],"sourceRoot":""}]);
